*{margin:0px;padding:0px}html{background-color:#f8f7f7}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

.home{position:relative;width:100vw;height:100vh}.home__content{position:relative}.home__hero{display:flex;justify-content:center;align-items:center;width:auto;height:50vh;flex-wrap:wrap;flex-direction:column}.home__hero h1{font-size:3.8rem;width:50vw;text-align:center;font-family:"Montserrat",sans-serif}.home::before{content:"";position:absolute;top:0;left:0;width:100%;height:100%;background-image:url(/static/media/background.810729e1.jpg);background-repeat:no-repeat;background-size:cover;-webkit-filter:grayscale(100%);filter:grayscale(100%);opacity:20%}

.navbar{width:auto;display:flex;justify-content:space-between;font-family:"Montserrat",sans-serif;font-weight:600;font-size:18px;padding:3vw 10vw;line-height:20px}.navbar a{color:#000;text-decoration:none}.navbar a:visited{color:#000}.navbar__middle,.navbar__right{display:flex;justify-content:space-between}.navbar__dot{padding:0 15px}

.timeSelect{display:flex;width:100%;justify-content:center}.timeSelect__container{margin-top:15vh;background-color:#f7f5fb;width:60%;height:70px;box-shadow:5px 5px 15px 5px #00000034;border-radius:5px;display:flex;max-width:800px;padding-left:3vw}.timeSelect__content-select{display:flex;justify-content:space-between;align-items:center;width:100%;height:100%}.timeSelect__content-button{display:flex;justify-content:flex-end;align-items:center;padding-right:3vw;width:100%}

.dropdown{position:relative;display:inline-block}.dropdown__button{background-color:transparent;border:none;border-bottom:black solid 2px;font-family:"Montserrat",sans-serif;font-weight:600;font-size:15px;line-height:20px;width:150px;text-align:left;background-image:url('data:image/svg+xml;utf8,<svg aria-hidden="true" focusable="false" data-prefix="fas" data-icon="chevron-down" class="svg-inline--fa fa-chevron-down fa-w-14" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 448 512"><path fill="currentColor" d="M207.029 381.476L12.686 187.132c-9.373-9.373-9.373-24.569 0-33.941l22.667-22.667c9.357-9.357 24.522-9.375 33.901-.04L224 284.505l154.745-154.021c9.379-9.335 24.544-9.317 33.901.04l22.667 22.667c9.373 9.373 9.373 24.569 0 33.941L240.971 381.476c-9.373 9.372-24.569 9.372-33.942 0z"></path></svg>');background-repeat:no-repeat;background-position-y:60%;background-position-x:98%;background-size:12px;margin-right:20px}.dropdown__button:hover+.dropdown__content{display:block}.dropdown__content{display:none;position:absolute;background-color:#f7f5fb;width:140px;box-shadow:0px 8px 16px 0px rgba(0,0,0,0.2);z-index:1;padding:5px;cursor:pointer;font-family:"Montserrat",sans-serif;font-weight:500;font-size:15px}

.button__button{display:inline-block;background-color:#0063d9;border:none;color:white;padding:10px 42px;text-align:center;text-decoration:none;display:inline-block;font-size:16px;border-radius:5px;font-family:"Montserrat",sans-serif;font-weight:500}.button__button:hover{background-color:rgba(0,98,217,0.854902)}

